const fotom = document.getElementById("fotom");
const fotosa = document.getElementById("fotosa");
const fotor = document.getElementById("fotor");
const fotop = document.getElementById("fotop");

function cambiaportada(id) {
    let src = id.src;
    const m = src.split("/")
    const s = parseInt(m.pop().substring(1, 2));
    const p = parseInt(m.pop().substring(1, 1));
    let k = 0;
    if (s == 3) {
        k = 0;
    } else {
        k = s + 1;
    }
    src = src.replace(`${ s.toString() }.`, `${ k.toString() }.`);
    id.src = src;


}
fotom.addEventListener('click', () => {
    cambiaportada(fotom)
})
fotosa.addEventListener('click', () => {
    cambiaportada(fotosa)
})

fotor.addEventListener('click', () => {
    cambiaportada(fotor)
})

fotop.addEventListener('click', () => {
    cambiaportada(fotop)
})